<template>
  <div class="content">
    <el-form ref="form" :model="formData" label-width="80px">
      <el-form-item label="旧密码">
        <el-input type="password" v-model="formData.pwd"></el-input>
      </el-form-item>
      <el-form-item label="新密码">
        <el-input type="password" v-model="formData.newpwd"></el-input>
      </el-form-item>
      <el-form-item label="确认新密码">
        <el-input type="password" v-model="formData.newPwd"></el-input>
      </el-form-item>
      <el-form-item>
        <el-button type="primary" @click="change">修改</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
    export default {
        name: "change-pwd",
      data(){
          return{
            formData:{
              pwd: '',
              newpwd: '',
              newPwd: ''
            }
          }
      },
      methods:{
          change(){
            if (this.formData.newpwd === this.formData.newPwd) {
              this.$axios.post('changePwd', this.formData).then(res => {
                if(res.code==200){
                  this.$message({
                    type: 'success',
                    message: '修改成功!'
                  });
                  setTimeout(()=>{
                    this.$router.push("/login")
                  },500)
                }else{
                  this.$message({
                    type: 'warning',
                    message: res.msg
                  });
                }
              })
            } else {
              alert('两次密码不一致，请重新输入')
            }
          }
      }
    }
</script>

<style scoped>

</style>



